<template>

  <div id="consulting">
    <div class="problem">
      <my-title :title="'常见问题'" :more_title="'查看更多'" @clickP="to('questions','常见问题')"></my-title>
      <my-problem-p v-for="(item , index) in problems" :data="item" :index="index" :key=index></my-problem-p>
    </div>
    <div class="onlineService">
      <my-title :title="'在线客服'" :more_title="'查看更多'" @clickP="to('onlineService','在线客服')"></my-title>
      <div ref="onlineService_portrait" @click="consult(item)" v-for="(item,index) in staff" :key=index
           class="onlineService_portrait">
        <div v-if="item.online" class="portrait_header">
          <img :src="portraitImg">
        </div>
        <div v-else style="background-color: #999999" class="portrait_header">
          <img :src="portraitImg">
        </div>
        <div class="portrait_name">{{item.name}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import myTitle from '../../../../components/myTitle'
import myProblemP from '../../../../components/myProblemP'

export default {
  name: 'consulting',
  components: {
    myTitle,
    myProblemP
  },
  data () {
    return {
      portraitImg: require('@/common/images/客服.jpg'),
      problems: [
        {
          title: '创业板转签如何办理',
          id: 23156,
          clickN: 12345,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何办理',
          id: 23156,
          clickN: 12345,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何办理',
          id: 23156,
          clickN: 12345,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何办理',
          id: 23156,
          clickN: 12345,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何办理',
          id: 23156,
          clickN: 12345,
          time: '2019/11/15 10:36:55'
        }
      ],
      staff: [
        {
          online: true,
          name: '服务人员',
          id: '111'
        }, {
          online: true,
          name: '服务人员',
          id: '11'
        }, {
          online: true,
          name: '服务人员',
          id: '112'
        }, {
          online: true,
          name: '服务人员',
          id: '113'
        }, {
          online: false,
          name: '服务人员',
          id: '114'
        }
      ]
    }
  },
  mounted () {
    this.query()
  },
  methods: {
    query () {
      this.findByPageQ()
      this.advisorHomeList()
    },
    // 分页查询常见问题
    findByPageQ () {
      this.$http.findByPageQ({
        sortType: 0,
        sort: 0,
        page: 0,
        size: 8
      }).then((res) => {
        console.log(res)
      })
    },
    // 获取在线客服首页列表
    advisorHomeList () {
      this.$http.advisorHomeList({
      }).then((res) => {
        console.log(res)
      })
    },
    to (name, title) {
      this.$store.commit('setCurrentTitle', title)
      this.$router.push({
        name: name,
        params: {
          title: title
        }
      })
    },
    consult (item) {
      this.$store.commit('setCurrentTitle', '咨询')
      this.$router.push({
        name: 'consult',
        params: {
          serviceID: item.id
        }
      })
    }
  }
}
</script>

<style scoped>
  #consulting {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #F6F7F8;
  }

  #consulting .problem {
    position: relative;
    width: 100%;
    background-color: #FFFFFF;
  }

  #consulting .onlineService {
    position: relative;
    width: 100%;
    margin-top: 10px;
    background-color: #FFFFFF;
  }

  #consulting .onlineService .onlineService_portrait {
    display: inline-block;
    width: 4rem;
    height: 4rem;
    padding: 1.7rem 0.5rem;
  }

  #consulting .onlineService .onlineService_portrait .portrait_header {
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
    background-color: #90dfad;
  }

  #consulting .onlineService .onlineService_portrait .portrait_header img {
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
  }

  #consulting .onlineService .onlineService_portrait .portrait_name {
    color: black;
    font-size: 0.8rem;
  }
</style>



