<template>
  <div id="myProblemP" @click="clickP">
    {{index+1+'、'+data.title}}
  </div>
</template>

<script>
export default {
  name: 'myProblemP',
  props: {
    data: Object,
    index: Number
  },
  data () {
    return {}
  },
  mounted () {
  },
  methods: {
    clickP () {
      this.$store.commit('setCurrentArticle', this.data)
      this.$router.push({
        name: 'answer',
        params: this.data.id
      })
    }
  }
}
</script>

<style scoped>
  #myProblemP {
    height: 2rem;
    line-height: 2rem;
    font-size: 1.09rem;
    color: black;
    padding: 0.9rem;
    border-bottom: 0.5px solid #EEEEEE;
  }
</style>



